@import '../../variables';

.tags-input {
  display: flex;
  flex-flow: row wrap;
  align-items: center;

  &__item {
    &--hidden {
      display: none; }

    & + & {
      margin-left: 5px; } }

  &__input {
    width: 80px;
    border-color: $input-default-border-color;
    border-style: solid;
    border-width: 0;
    border-bottom-width: 1px;
    outline: none;

    &:focus {
      border-color: $input-default-border-color-focused; } } }
