.note-form {
  padding: 15px;

  &__block {
    & + & {
      margin-top: 15px; } }

  &__body {
    min-height: 150px; }

  &__footer {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    margin-top: 5px; }

  &__tags {
    max-width: 50%; }

  &__colors {
    margin-left: auto;
    margin-right: 100px; } }
