.app {
  display: block;
  position: relative;
  width: 100%;
  height: 100vh;
  padding-top: 65px;
  box-sizing: border-box;

  &__header {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 60px; }

  &__main {
    position: realtive;
    width: 100%;
    height: 100%;
    padding: 0 15px 15px 15px;
    box-sizing: border-box; } }

.navbar {
  display: flex;
  width: 100%;
  height: 60px;
  flex-flow: row nowrap;
  align-items: center;
  background-color: #fafafa;
  border-bottom: 1px solid #eee;

  .navbar__brand {
    height: 60px;
    padding: 0 15px;
    margin: 0;
    font-size: 16px;
    line-height: 60px;
    white-space: nowrap; } }

.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);

  &--opened {
    display: flex; }

  .overlay__content {
    width: 600px;
    max-width: 100%;
    padding: 15px;
    background-color: #fff;
    border-radius: 10px; } }

.search-tags-input {
  width: 200px;
  margin-left: auto;
  margin-right: 15px; }
