.note {
  display: block;
  position: relative;
  width: 200px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);

  &__title {
    margin: 0;
    padding: 15px 15px 0 15px; }

  &__body {
    margin: 0;
    padding: 10px 15px 0 15px; }

  &__tags {
    padding: 5px 15px 0px 15px;

    .tag + .tag {
      margin-left: 8px; } }

  &__footer {
    display: flex;
    flex-flow: row-nowrap;
    padding: 0 5px 5px 5px;
    justify-content: flex-end;
    opacity: 0;
    transition: opacity 0.2s ease; }

  &--color-yellow {
    background-color: #fff9c4; }

  &--color-blue {
    background-color: #bbdefb; }

  &--color-green {
    background-color: #c8e6c9; }

  &--unfocus {
    opacity: 0.6; }

  &:hover {
    .note__footer {
      opacity: 1; } } }

.tag {
  color: #555;
  font-size: 12px;
  font-weight: bold;
  cursor: pointer;

  &:hover {
    color: #333; } }
