.color-option {
  display: inline-block;
  width: 22px;
  height: 22px;
  border-width: 0;
  border-radius: 3px;
  cursor: pointer;
  outline: none;

  &__label {
    display: none; }

  &--yellow {
    background-color: #fff9c4;

    &.color-option--selected {
      box-shadow: 0 0 0 2px #fdd835; } }

  &--blue {
    background-color: #bbdefb;

    &.color-option--selected {
      box-shadow: 0 0 0 2px #64b5f6; } }

  &--green {
    background-color: #c8e6c9;

    &.color-option--selected {
      box-shadow: 0 0 0 2px #81c784; } } }
