@import "./variables";

body {
  display: block;
  position: relative;
  padding: 0;
  margin: 0;
  color: $default-text-color;
  background-color: $body-bg-color;
  font-family: $font-family-default;
  font-size: $font-size-default; }

button {
  color: $default-text-color;
  font-family: $font-family-controls;
  font-size: $font-size-default; }

input, textarea {
  box-sizing: border-box;
  color: $default-text-color;
  font-family: $font-family-default;
  font-size: $font-size-default; }

textarea {
  resize: none; }

.btn {
  padding: 5px 10px;
  background-color: transparent;
  border-width: 0;
  border-radius: 2px;
  font-size: $font-size-default;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
  outline: none;

  &:hover, &:focus {
    background-color: rgba(0, 0, 0, 0.2); }

  &--sm {
    padding: 3px 10px;
    border-radius: 1px;
    font-size: $font-size-sm; }

  &--lg {
    padding: 10px 10px;
    border-radius: 3px;
    font-size: $font-size-lg; }

  &--ghost {
    color: lighten($default-text-color, 60%);

    &:hover {
      color: $default-text-color; } } }

.input-ghost {
  width: 100%;
  padding: 5px 10px;
  box-sizing: border-box;
  background-color: transparent;
  border-color: $input-default-border-color;
  border-style: solid;
  border-width: 0;
  border-bottom-width: 1px;
  font-size: $font-size-default;
  outline: none;
  transition: border-color 0.2s ease;

  &:focus {
    border-color: $input-default-border-color-focused; }

  &--xl {
    padding: 10px 10px;
    font-size: $font-size-xl; } }

.textarea-ghost {
  width: 100%;
  padding: 5px 10px;
  box-sizing: border-box;
  background-color: transparent;
  border-color: $input-default-border-color;
  border-style: solid;
  border-width: 1px;
  outline: none;
  resize: none;
  transition: border-color 0.2s ease;

  &:focus {
    border-color: $input-default-border-color-focused; }

  &--lg {
    padding: 10px 10px;
    font-size: $font-size-lg; } }
