@import '../../variables';

.tag-control {
  display: inline-block;
  padding: 0 0 0 5px;
  border-radius: 3px;
  font-family: $font-family-controls;
  font-size: 12px;
  font-weight: bold;
  cursor: default;

  &__remove {
    padding: 3px 5px;
    color: lighten($default-text-color, 60%);
    background-color: transparent;
    border-width: 0;
    font-family: $font-family-controls;
    font-size: 12px;
    font-weight: bold;
    cursor: pointer;
    opacity: 0;
    outline: none;

    &:hover, &:focus {
      color: $default-text-color; } }

  &:hover {
    background-color: #eee;

    .tag-control__remove {
      opacity: 1; } } }
